@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:100,300&display=swap");

$orange-colour: #ca9b5f;
$blue-colour: #20203c;

* {
  padding: 0;
  margin: 0;
}
#root {
  height: 100%;
  width: 100%;
}
html {
  width: 100%;
  height: 100%;
}
body {
  font-family: "Josefin Sans", sans-serif;
  background-color: #fdfbfb;
}
.father-content {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  .title {
    margin: 5vh 0 10vh 0;
    color:  $orange-colour;
    text-transform: uppercase;
    font-size: 1.3em;
    text-align: center;
    user-select: none;
    font-weight: 500;
    font-family: "Josefin Sans", sans-serif;
  }
  .banner {
    text-align: center;
    font-size: 1.5rem;
  }

  .main-content {
    flex-direction: column;
    width: 60vw;
    height: 60vh;
    background-image: url(/assets/texture.png);
    box-shadow: 0 1em 2em 1em rgb(201, 201, 201);

    .search-and-subtitle {
      display: flex;
      justify-content: space-around;

      .movies-table {
        line-height: 2.5;
        color: $blue-colour;
        font-size: 1rem;
        width: 48vh;
        height: 20vh;

        .input-search {
          outline: none;
          border: none;
          background-color: #fdfbfb;
          background-image: url(/assets/texture.png);
          border-bottom: 1px solid $blue-colour;
          width: 15vw;
          font-size: 1rem;
          margin-bottom: 30px;
          color: $blue-colour;
        }
      }

      .nominated-movies {
        width: 47vh;
        height: 20vh;

        h3 {
          margin-bottom: 15px;
          text-transform: uppercase;
          color: $blue-colour;
          padding-top: 12px;
        }

        .nominations {
          line-height: 2.5;
          color: $blue-colour;
          margin-top: 30px;
        }
      }
    }
  }
}
.button-nominate-remove {
  background-color: white;
  padding: 4px;
  font-size: 0.8rem;
  letter-spacing: 1px;
  border: none;
  box-shadow: 0 1px 2px 1px rgb(201, 201, 201);

  &:hover {
    background-color: rgb(238, 234, 234);
  }
}
.left-border {
  width: 1em;
  height: 30.8em;
  background-color: $orange-colour;
}
.right-border {
  width: 1em;
  height: 30.8em;
  border-left: 1px solid $orange-colour;
  background-color: $orange-colour;
  z-index: 1;
}

@media only screen and (max-width: 1440px) and (max-height: 900px) {
  .left-border {
    height: 25.8em;
  }
  .right-border {
    height: 25.8em;
  }
}
@media only screen and (min-width: 375px) and (max-width: 414px) and (min-height: 667px) and (max-height: 812px) {
  .body{
      width:100%;
      height: 100%;
  }
    .father-content {
    .title {
      margin: 4vh 0 5vh 0;
    }
    .main-content {
      width: 100vw;
      height: 100vh;
      overflow: visible;

      .search-and-subtitle {
        display: inline-block;

        .movies-table {
          line-height: 3;
          font-size: 0.8rem;
          padding-left: 5%;

          .input-search {
            width: 60vw;
          }
        }
      }
      .nominated-movies {
        margin-top: 50%;
        width: 90vw;
        font-size: 14px;
        padding-left: 5%;
      }
    }
  }
  .button-nominate-remove {
    padding: 0;
    font-size: 0.7rem;
    letter-spacing: 0;
  }
  .left-border,
  .right-border {
    width: 0;
    height: 0;
  }
}

@media only screen and (min-width:667px) and(max-width: 736px)  and (min-height:375px) and (max-height: 414px) {
    .body{
        width:100%;
        height: 100%;
    }
      .father-content {
      .title {
        margin: 4vh 0 5vh 0;
      }
      .main-content {
        width: 100vw;
        height: 100vh;
        box-shadow: none;
  
        .search-and-subtitle {
         

          .movies-table {
            line-height: 3;
            font-size: 0.8rem;
  
            .input-search {
              width: 25vw;
            }
          }
        }
        .nominated-movies {
          width: 90vw;
          font-size: 14px;
        }
      }
    }
    .button-nominate-remove {
      padding: 0;
      font-size: 0.7rem;
      letter-spacing: 0;
    }
    .left-border,
    .right-border {
      width: 0;
      height: 0;
    }
}
