ight:25.8em}.right-border{height:25.8em}}@media only screen and (min-width: 375px)and (max-width: 414px)and (min-height: 667px)and (max-height: 812px){.body{width:100%;height:100%}.father-content .title{margin:4vh 0 5vh 0}.father-content .main-content{width:100vw;height:100vh;overflow:visible}.father-content .main-content .search-and-subtitle{display:inline-block}.father-content .main-content .search-and-subtitle .movies-table{line-height:3;font-size:.8rem;padding-left:5%}.father-content .main-content .search-and-subtitle .movies-table .input-search{width:60vw}.father-content .main-content .nominated-movies{margin-top:50%;width:90vw;font-size:14px;padding-left:5%}.button-nominate-remove{padding:0;font-size:.7rem;letter-spacing:0}.left-border,.right-border{width:0;height:0}}@media only screen and (min-width: 667px)and (max-width: 736px)and (min-height: 375px)and (max-height: 414px){.body{width:100%;height:100%}.father-content .title{margin:4vh 0 5vh 0}.father-content .main-content{width:100vw;height:100vh;box-shadow:none}.father-content .main-content .search-and-subtitle .movies-table{line-height:3;font-size:.8rem}.father-content .main-content .search-and-subtitle .movies-table .input-search{width:25vw}.father-content .main-content .nominated-movies{width:90vw;font-size:14px}.button-nominate-remove{padding:0;font-size:.7rem;letter-spacing:0}.left-border,.right-border{width:0;height:0}}
